<template>
    <div class="col-md-6" style="border: 1px solid black;">
        <header>
            <slot name="header"></slot>
        </header>
        <main>
            <slot></slot>
        </main>
        <div>
            <slot name="slotWithData" :user="user" :something="something">
                {{ user.firstName }}
            </slot>
        </div>
        <footer>
            <slot name="footer"></slot>
        </footer>

    </div>
</template>

<script>
    export default {
        name: "my-slot",
        data(){
            return{
                user: {
                    firstName: 'basir',
                    lastName: 'vali',
                },
                something: ''
            }
        },
    }
</script>

