<template>
    <div class="sandbox">
        <h1 class="subheading grey--text">Sandbox components</h1>
        <v-container class="my-5">
            <div @click="showMySlot = !showMySlot">
                <v-btn flat small :color="!showMySlot ? 'primary':'error'">slots</v-btn>
            </div>
            <my-slot v-if="showMySlot">
                <p>all defaults text goes to slots</p>
                <!--old method-->
                <!--<template slot="header">header</template>-->
                <template v-slot:header>new method</template>
                <!--<div slot="footer">old footer</div>-->
                <!--v-slot can be used only with template tag-->
                <!--slot props pass child data to the parent component so we can decide what to use in parent (also known as scoped slot)-->
                <template v-slot:slotWithData="slotProps">{{ slotProps.user.lastName }}</template>
                <template v-slot:footer><span style="color: red">footer</span></template>
            </my-slot>
        </v-container>
    </div>
</template>

<script>

    import MySlot from "./my-slot";

    export default {
        data() {
            return {
                showMySlot: false,
            }
        },
        components: {
            MySlot
        }
    }
</script>
